@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
    padding: 5rem 0;
    position: relative;
    text-wrap: pretty;
    background-repeat: no-repeat;
    background-size: cover;

    @include m.tablet{
        min-height: 70rem;
    }

    &::before{
        content: '';
        background-color: v.$negro;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        opacity: .6;
    }
    
    .contenido-header{
        position: relative;

        h1{
            text-align: center;
            color: v.$blanco;
            text-transform: uppercase;
            margin-top: 5rem;
            @include m.tablet {
                margin-top: 10rem;
            }
        }
    }
}

//header de cada pagina
.header-inicio{
    background-image: url('../img/header_inicio.avif');
}

.header-nosotros{
    background-image: url('../img/header_nosotros.avif');
}

.header-proceso{
    background-image: url('../img/header_proceso.avif');
}

.header-menu{
