@use './variables' as v;

@mixin telefono {
    @media( min-width: v.$telefono ) {
        @content
    }
};

@mixin tablet {
    @media( min-width: v.$tablet ) {
        @content
    }
};

@mixin desktop {
    @media( min-width: v.$desktop ) {
        @content
    }
}

@mixin grid( $columnas: 3, $espaciado: 2rem){
    display: grid;
    grid-template-columns: repeat( $columnas, 1fr );
    gap: $espaciado;
}

